<template>
  <div class="search">
    <p>
      <input type="text" placeholder="搜索歌曲、 歌手、 专辑" />
      <span></span>
    </p>
    <div class="text">热门搜索</div>
    <ul class="hotSearch">
        <router-link v-for="(item, index) in hotSearch" :key="index" to="/" tag="li">
            <!-- <div class="content"></div> -->
        </router-link>
    </ul>
  </div>
</template>


<script>
export default {
    name: 'Search',
    data(){
        return {
            hotSearch: [],
        }
    },
    beforeRouteEnter(to,from,next){
        console.log(1111);
        next(vm=>{
            vm.$http.get('/search/hot').then(data=>{
                console.log(data);
                vm.hotSearch = data.data.result;
            })
        })
    }
}
</script>

<style lang="less" scoped>
.search {
  p {
    position: relative;
    display: flex;
    height: 60px;
    border-bottom: 1px solid #ddd;
    justify-content: space-around;
    align-items: center;
    input {
      outline: none;
      border: 0;
      height: 30px;
